.btn-upload {
    position: relative;
  }
  
  .btn-upload > input[type="file"] {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    top: 0;
    left: 0;
    cursor: pointer;
  }
  
  .secondary-content {
    cursor: pointer;
  }
  
  .dropzone {
    position: relative;
    min-height: 200px;
    padding: 10px 10px;
    position: relative;
    outline: 2px dashed grey;
    cursor: pointer;
    outline-offset: -10px;
    background: #f5f5f5;
    border-radius: 8px;
    transition: all 0.25s ease;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .dropzone:hover {
    box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14),
      0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
  }
  
  .dropzone .input-dropzone {
    position: absolute;
    width: 100%;
    height: 100%;
    cursor: pointer;
    top: 0;
    left: 0;
    opacity: 0;
  }
  
  .dropzone .call-to-action {
    font-size: 1.2rem;
    text-align: center;
  }
  
